import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from './containers/Home.vue';
import Blog from './containers/Blog.vue';
import BlogList from './containers/BlogList.vue';
Vue.use(VueRouter);
var routes = [{
  path: '/',
  name: 'home',
  component: Home
}, {
  path: '/blog/:id',
  name: 'blog',
  component: Blog
}, {
  path: '/blog',
  name: 'bloglist',
  component: BlogList
}];
var router = new VueRouter({
  mode: 'history',
  routes: routes
});
export default router;