export var title = "var/const/let의 차이점";
export var postDate = "2021-03-20";
export var description = "Javascript에서 let/const/var의 차이점을 알려줍니다.";
export var content = "안녕하세요, [유튜브](https://www.youtube.com/c/다이트로)에서 활동하는 다이트로라고 합니다.\n\n자바스크립트에서 변수를 정의하는 스코프(Scope)들인데요.\n스코프란 변수의 접근성을 애기합니다.\nvar는 함수 스코프고 const과 let은 블록 스코프입니다.\n\n# 함수 스코프\n\n함수 스코프는 같은 함수 내에서만 접근이 가능 변수를 말합니다.\n\n```javascript\nfunction foo() {\n  var description = \"Hello, world!\";\n  if (true) {\n    var dytro = \"nice\";\n  }\n\n  console.log(description); // 접근 가능함\n  console.log(dytro); // 접근 가능함\n}\n\nfoo();\n\nconsole.log(description); // 접근 못함\nconsole.log(dytro); // 접근 못함\n```\n\n위 코드와 같이 같은 함수 내에 있는 변수들만 인식이 가능한 것이 보입니다.\n\n# 블록 스코프\n\n블록 스코프는 같은 블록 즉 같은 {} 내에 있는 것만 접근이 가능한 변수를 말합니다.\n\n```javascript\nfunction foo() {\n  const description = \"Hello, world!\";\n  if (true) {\n    const dytro = \"nice\";\n  }\n\n  console.log(description); // 접근 가능함\n  console.log(dytro); // 접근 못함\n}\n\nfoo();\n\nconsole.log(description); // 접근 못함\nconsole.log(dytro); // 접근 못함\n```\n\n위 코드와 같이 같은 블록 내에만 있는 변수들만 인식이 가능한 것이 보입니다.\n\n# const/let의 차이점\n\n이제 var과 const/let의 차이점은 알겠는데, const과 let의 차이점은 뭘까요?\n\nlet은 재지정이 가능한데, const는 불가능합니다.\n\n```javascript\nlet age = 15;\nconst hasGirlfriend = false;\n\nage = 16; // 할 수 있음\nhasGirlfriend = true; // 할 수 없음\n```\n\n위 코드를 보면 이미 선언된 const 값은 재지정을 못 합니다. 원주율 같이 변하지 않는 값에 이용합니다.\n\n하지만, const도 변수 내에 값은 변경할 수 있습니다.\n\n```javascript\nconst learnedLanguages = [\n  \"Java\",\n  \"Javascript\",\n  \"Lua\",\n  \"C\",\n  \"C++\",\n  \"C#\",\n  \"Ruby\",\n  \"Python\",\n];\n\nlearnedLanguages = []; // 할 수 없음\nlearnedLanguages.push(\"Kotlin\"); // 할 수 있음\n```\n\n위 코드에선 이 배열을 비어있는 배열로 재지정을 할 수는 없지만 'Kotlin'은 추가할 수 있습니다.\n\n# 마무리\n\n물론, 차이점은 더 있지만 기본적인 차이점은 이제 알았습니다. 그럼 즐거운 코딩하세요.\n";